import React from 'react';

export default class Control extends React.Component {
    constructor(props) {
        super(props)
        this.choicesChengeHandler = this.choicesChengeHandler.bind(this);
        this.deleteChoiceHandler = this.deleteChoiceHandler.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.addData = this.addData.bind(this);
        this.state = {
            unit: "",
            question: "",
            choices: ["", "", "", "", ""],
            answer: 1,
            comment: "",
            url: ""
        }
    }

    choicesChengeHandler(index, choice) {
        let choices = this.state.choices.slice();
        choices[index] = choice;
        this.setState({ choices })
    }

    handleChange = (event) => {
        this.setState({
            unit: event.target.unit,
            question: event.target.question,
            answer: event.target.answer,
            comment: event.target.answer,
            url: event.target.url
        })
    }

    addChoices() {
        let choices = this.state.choices.slice();
        choices.push("");
        this.setState({ choices })
    }

    deleteChoiceHandler(index) {
        let choices = this.state.choices.slice();
        choices = [...choices.slice(0, index), ...choices.slice(index + 1)]
        this.setState({ choices })
    }

    createAnswer() {
        const choiceList = this.state.choices;
        const answer = choiceList.map((choice, index) => {
            return (
                <option key={index} value={index + 1}>{index + 1}</option>
            )
        });
        return (
            <select name="answer" value={this.state.answer} onChange={this.handleChange}>
                {answer}
            </select>
        );
    }

    addData() {
        const choices = this.state.choices.slice().map((choice, index) => {
            return (
                { no: index + 1, choice: choice }
            )
        })
        const content = {
            id: (new Date()).getTime,
            unit: "Java",
            question: this.state.question,
            choices: choices,
            answer: 5,
            comment: "0bが付くと2進数として認識してくれます。",
            url: ""
        }
        this.props.addData(content);
    }

    render() {
        return (
            <div>
                <table>
                    <tbody>
                        <tr>
                            <th>単元</th>
                            <th>
                                <select name="unit" value={this.state.unit} onChange={this.handleChange}>
                                    <option value="Java">Java</option>
                                    <option value="Oracle">Oracle</option>
                                    <option value="HTML/CSS">HTML/CSS</option>
                                </select>
                            </th>
                        </tr>
                        <tr>
                            <th>問題文</th>
                            <th>
                                <textarea name="question" value={this.state.question} onChange={this.handleChange} />
                            </th>
                        </tr>
                        {this.state.choices.map((choice, index) => (
                            <Choices key={index} index={index} choice={choice}
                                changeChoice={this.choicesChengeHandler} deleteChoice={this.deleteChoiceHandler} />
                        ))}
                        <tr>
                            <th>答え</th>
                            <th>
                                {this.createAnswer()}
                            </th>
                        </tr>
                        <tr>
                            <th>解説</th>
                            <th>
                                <textarea name="comment" value={this.state.comment} onChange={this.handleChange} />
                            </th>
                        </tr>
                        <tr>
                            <th>URL</th>
                            <th>
                                <input type="text" name="url" value={this.state.url} onChange={this.handleChange}></input>
                            </th>
                        </tr>

                    </tbody>
                </table>
                <button onClick={() => { this.addChoices() }}>選択肢を増やす</button>
                {/* <button onClick={() => { this.addData() }}>追加</button> */}
            </div>
        )
    }
}

class Choices extends React.Component {
    constructor(props) {
        super(props);
        this.changeChoice = this.changeChoice.bind(this);
        this.deleteChoice = this.deleteChoice.bind(this);
    }

    changeChoice(e) {
        this.props.changeChoice(this.props.index, e.target.value);
    }

    deleteChoice(index) {
        this.props.deleteChoice(index);
    }

    render() {
        return (
            <tr>
                <th>{this.props.index + 1}</th>
                <th>
                    <input type="text" value={this.props.choice} onChange={this.changeChoice} />
                    <button onClick={() => { this.deleteChoice(this.props.index) }}>削除</button>
                </th>
            </tr>
        )
    }
}